import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import GitHubButton from 'react-github-btn';
import styled from 'react-emotion';
import Link from './Link';
import ImageLogo from './images/ImageLogo';
import './styles.css';
import config from '../../config.js';
import Search from './Search';
import Sidebar from './Sidebar';

const help = require('./images/help.svg');
const textLogo = require('./images/logo-text.png');
const isSearchEnabled = config.header.search && config.header.search.enabled ? true : false;

let searchIndices = [];
if (isSearchEnabled && config.header.search.indexName) {
  searchIndices.push({
    name: `${config.header.search.indexName}`,
    title: `Results`,
    hitComp: `PageHit`,
  });
}

const SidebarContainer = styled('div')`
  position: relative;
  height: calc(100vh - 100px);
  overflow: auto;
  background-color: #fafbfc;
`;

const Header = () => (
  <StaticQuery
    query={graphql`
      query headerTitleQuery {
        site {
          siteMetadata {
            headerTitle
            githubUrl
            helpUrl
            logo {
              link
              image
            }
            headerLinks {
              link
              text
            }
          }
        }
      }
    `}
    render={({ site }) => <Contents site={site} />}
  />
);

const Contents = ({ site }) => {
  const {
    siteMetadata: { headerTitle, githubUrl, helpUrl, logo, headerLinks },
  } = site;
  const finalLogoLink = logo.link !== '' ? logo.link : '/';
  let logoImageFragment, titleFragment;

  if (logo.image !== '') {
    logoImageFragment = (
      <img className={'img-responsive imageLogo'} src={logo.image} alt={'Image Logo'} />
    );
  } else {
    logoImageFragment = <ImageLogo />;
  }

  if (headerTitle) {
    titleFragment = (
      <div className={'headerTitle'} dangerouslySetInnerHTML={{ __html: headerTitle }} />
    );
  } else {
    titleFragment = (
      <div className={'headerTitle'}>
        <img className={'textLogo'} src={textLogo} alt={'Text Logo'} />
      </div>
    );
  }

  return (
    <div className={'navBarWrapper'}>
      <nav className={'navbar navbar-expand-lg navbar-default navBarDefault'}>
        <div className={'navbar-header navBarHeader'}>
          <Link to={finalLogoLink} className={'navbar-brand navBarBrand'}>
            {logoImageFragment}
            {titleFragment}
          </Link>
          <button
            type="button"
            className={'navbar-toggle collapsed navBarToggle'}
            data-toggle="collapse"
            data-target="#navbar"
            aria-expanded="false"
            aria-controls="navbar"
          >
            <span className={'sr-only'}>Toggle navigation</span>
            <span className={'icon-bar'}></span>
            <span className={'icon-bar'}></span>
            <span className={'icon-bar'}></span>
          </button>
        </div>
        {isSearchEnabled ? (
          <div className={'searchWrapper navBarUL'}>
            <Search collapse indices={searchIndices} />
          </div>
        ) : null}
        <div id="navbar" className={'navbar-collapse collapse navBarCollapse'}>
          <SidebarContainer>
            <Sidebar />
            <hr />
            {isSearchEnabled ? (
              <div className={'searchWrapper navBarUL'}>
                <Search collapse indices={searchIndices} />
              </div>
            ) : null}
          </SidebarContainer>
          <ul className={'nav navbar-nav navBarUL navBarNav navbar-right navBarULRight'}>
            {headerLinks.map((link, key) => {
              if (link.link !== '' && link.text !== '') {
                return (
                  <li key={key}>
                    <a
                      href={link.link}
                      target="_blank"
                      rel="noopener noreferrer"
                      dangerouslySetInnerHTML={{ __html: link.text }}
                    />
                  </li>
                );
              }
            })}
            {helpUrl !== '' ? (
              <li>
                <a href={helpUrl}>
                  <img src={help} alt={'Help icon'} />
                </a>
              </li>
            ) : null}
            {githubUrl !== '' ? <li className="divider"></li> : null}
            {githubUrl !== '' ? (
              <li className={'githubBtn'}>
                <GitHubButton href={githubUrl} data-show-count="true" aria-label="Star on GitHub">
                  Star
                </GitHubButton>
              </li>
            ) : null}
          </ul>
        </div>
      </nav>
    </div>
  );
};

export default Header;
