import React from 'react';
import OpenedSvg from '../images/OpenedSvg';
import ClosedSvg from '../images/ClosedSvg';
import config from '../../../config';
import Link from '../Link';

const TreeNode = ({
  className = '',
  setCollapsed,
  collapsed,
  slug,
  title,
  isRoot,
  isRootItem = false,
  items,
  location,
}) => {
  const isCollapsed = collapsed[slug];
  const collapse = () => {
    setCollapsed(slug);
  };
  const hasChildren = items.length !== 0;
  const active =
    location &&
    (location.pathname === slug || location.pathname === config.gatsby.pathPrefix + slug);
  const calculatedClassName = [
    `${className} item`,
    active ? ' active' : '',
    hasChildren ? ' hasChildren' : '',
    isRootItem ? ' isRootItem' : '',
  ].join('');

  return (
    <li className={calculatedClassName}>
      {!config.sidebar.frontLine && title && hasChildren ? (
        <button onClick={collapse} className="collapser">
          {!isCollapsed ? <OpenedSvg /> : <ClosedSvg />}
        </button>
      ) : null}

      {title && <Link to={slug}>{title}</Link>}

      {!isCollapsed && hasChildren ? (
        <ul>
          {items.map(item => (
            <TreeNode
              key={item.slug}
              setCollapsed={setCollapsed}
              collapsed={collapsed}
              isRoot={false}
              isRootItem={isRoot}
              {...item}
            />
          ))}
        </ul>
      ) : null}
    </li>
  );
};

export default TreeNode;
