import React, { Component } from 'react';
import Helmet from 'react-helmet';
import { graphql } from 'gatsby';
import MDXRenderer from 'gatsby-plugin-mdx/mdx-renderer';
import { injectGlobal } from 'react-emotion';
import { Layout } from '$components';
import NextPrevious from '../components/NextPrevious';
import '../components/styles.css';
import { getLayoutNodes } from '../utils/layout';
import config from '../../config';

injectGlobal`
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

  html, body {
    font-family: -apple-system,
      BlinkMacSystemFont,
      "Segoe UI",
      "Roboto",
      "Roboto Light",
      "Oxygen",
      "Ubuntu",
      "Cantarell",
      "Fira Sans",
      "Droid Sans",
      "Helvetica Neue",
      sans-serif,
      "Apple Color Emoji",
      "Segoe UI Emoji",
      "Segoe UI Symbol";

    font-size: 16px;
  }

  a {
    transition: color 0.15s;
    color: #337ab7;
  }
`;

export default class Docs extends Component {
  render() {
    const { data, children } = this.props;
    const { allMdx = {}, mdx = {}, site = {} } = data;
    const { body, fields: mdxFields = {}, frontmatter = {} } = mdx;
    const { siteMetadata = {} } = site;
    const { title: siteTitle } = siteMetadata;

    const nav = getLayoutNodes(allMdx.edges).map(({ slug, title }) => ({ title, url: slug }));

    // meta tags
    let metaTitle = frontmatter.metaTitle;
    if (!metaTitle) {
      metaTitle = frontmatter.title;
      if (siteTitle) metaTitle += ` | ${siteTitle}`;
    }

    const metaDescription = frontmatter.metaDescription;
    let canonicalUrl = config.gatsby.siteUrl;
    canonicalUrl =
      config.gatsby.pathPrefix !== '/' ? canonicalUrl + config.gatsby.pathPrefix : canonicalUrl;
    if (mdxFields.slug) {
      canonicalUrl = canonicalUrl + mdxFields.slug;
    }

    let mainFragment, nextPreviousFragment;
    if (body) {
      mainFragment = <MDXRenderer>{body}</MDXRenderer>;
      nextPreviousFragment = <NextPrevious mdx={mdx} nav={nav} />;
    } else {
      mainFragment = children;
    }

    return (
      <Layout {...this.props}>
        <Helmet>
          {metaTitle ? <title>{metaTitle}</title> : null}
          {metaTitle ? <meta name="title" content={metaTitle} /> : null}
          {metaDescription ? <meta name="description" content={metaDescription} /> : null}
          {metaTitle ? <meta property="og:title" content={metaTitle} /> : null}
          {metaDescription ? <meta property="og:description" content={metaDescription} /> : null}
          {metaTitle ? <meta property="twitter:title" content={metaTitle} /> : null}
          {metaDescription ? (
            <meta property="twitter:description" content={metaDescription} />
          ) : null}
          <link rel="canonical" href={canonicalUrl} />
        </Helmet>
        <div className={'titleWrapper'}>
          <h1 className={'title'}>{mdxFields.title}</h1>
        </div>
        <div className={'mainWrapper'}>{mainFragment}</div>
        <div className={'addPaddTopBottom'}>{nextPreviousFragment}</div>
      </Layout>
    );
  }
}

export const pageQuery = graphql`
  query($id: String!) {
    site {
      siteMetadata {
        title
      }
    }
    mdx(fields: { id: { eq: $id } }) {
      fields {
        id
        title
        slug
      }
      body
      tableOfContents
      parent {
        ... on File {
          relativePath
        }
      }
      frontmatter {
        title
        metaTitle
        metaDescription
      }
    }
    allMdx {
      edges {
        node {
          fields {
            slug
            title
          }
        }
      }
    }
  }
`;
