import React from 'react';
import Heading from './Heading';
import Text from './Text';
import Code from './Code';
import CodeBlock from './CodeBlock';
import Pre from './Pre';
import AnchorTag from './AnchorTag';

function getId(props) {
  const { children } = props;
  if (Array.isArray(children)) {
    return children[0].href;
  } else {
    return children.replace(/\s+/g, '').toLowerCase();
  }
}

/* eslint-disable react/display-name */
export default {
  h1: props => <Heading id={getId(props)} {...props} is="h1" fontSize={38.0} />,
  h2: props => <Heading id={getId(props)} {...props} is="h2" fontSize={32.0} />,
  h3: props => <Heading id={getId(props)} {...props} is="h3" fontSize={26.0} />,
  h4: props => <Heading id={getId(props)} {...props} is="h4" fontSize={3} />,
  h5: props => <Heading id={getId(props)} {...props} is="h5" fontSize={2} />,
  h6: props => <Heading id={getId(props)} {...props} is="h6" fontSize={1} />,
  p: props => <Text {...props} is="p" lineHeight={1.625} mt={3} mb={4} />,
  pre: Pre,
  code: CodeBlock,
  inlineCode: props => <Code {...props} />,
  a: props => <AnchorTag {...props} />,
  // TODO add `img`
  // TODO add `blockquote`
  // TODO add `ul`
  // TODO add `li`
  // TODO add `table`
};
